import { DatabaseUtils } from "../Utils/DatabaseUtils";

/**
 *
 * Mthode pour tlcharger le dernier Excel utilis pour mettre  jour l'aplication.
 * @returns {boolean} Vrai si le fichier a t tlcharg. Faux si le fichier n'a pas t tlcharg.
 */
export function downloadFile(): boolean {
  return DatabaseUtils.downloadFile();
}

/**
 * Mthode pour avoir la date du dernier update.
 * @returns {Promise<string>} La date et l'heure de la dernire mise  jour.
 */
export async function getLastUpdatedDate(): Promise<string> {
  return await DatabaseUtils.getDateFromAppStatus();
}
